
.chart-gallery-grid img{
    vertical-align: middle;
    max-width: 100%;
    border: 1px solid rgba(192,200,201,1);
    
}


.charts-gallery{
    
    margin-top: 20px;
    margin-bottom: 50px;
    max-height: calc(80vh - 100px);
    overflow-y: auto;
    overflow-x: hidden;
    display: flex;
    flex-wrap: wrap;
    /* justify-content: center; */
}

.chart-gallery-grid{
    margin: 16px 20px;
    min-width: 200px;
    max-width: 200px;

}

.galary-description{
  width: 100%;
  max-width: 200px;
  display: inline-flex;
  justify-content: space-between;
  margin-top: 10px;
}

.galary-button{
  display: flex;
  justify-content: center;
  align-items: center;
}

.galary-button > button{
    background: #F6F8FA;
    border: 1px solid rgba(192,200,201,1);
    border-radius: 3px;
    font-family: Arial-BoldMT;
    font-size: 12px;
    color: #000000;
    font-weight: 700;
    width: 60px;
    height: 23px;
}
.galary-button > button:hover{
  background-color: #CDD6DE;
  cursor: pointer;
}

.galary-word{
    font-family: Arial;
    color: #000000;
    text-align: justify;
    line-height: 24px;
    font-size: 12px;
    color: #000000;
    font-weight: 400;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
